import React from 'react'
import '../css/Navbar.css'


class Navbar extends React.Component{
    constructor(){
        super()
        this.state = {
            companyName: "",
            navItems: []
        }
        this.loadNavbar = this.loadNavbar.bind(this)
    }
    loadNavbar(){
        this.setState({
            companyName: this.props.companyName,
            navItems: this.props.navItems
        })
    }
    componentDidMount(){
        this.loadNavbar()
    }
    render(){
        return(
            <nav className="navbar navbar-expand-sm navbar-dark bg-dark fixed-top">
                <a className="navbar-brand">{this.state.companyName}</a>
                <button className="navbar-toggler hidden-lg-up" type="button" data-toggle="collapse" data-target="#collapsibleNavId" aria-controls="collapsibleNavId"
                    aria-expanded="false" aria-label="Toggle navigation">
                    <i className="fa fa-bars" />
                </button>
                <div className="collapse navbar-collapse" id="collapsibleNavId">
                    <ul className="navbar-nav ml-auto">
                        {this.state.navItems.map((item, i)=> <NavbarItem key={i} item={item}/>)}
                        <li className="nav-item">
                            <a className="nav-link" href="https://medium.com/artiwiz-technologies">BLOG</a>
                        </li>
                    </ul>
                </div>
            </nav>
        )
    }
}

class NavbarItem extends React.Component{
    render(){
        let className = "nav-item"
        if(this.props.item.isactive){
            className+=" active"
        }
        return(
            <li className={className}>
                <a className="nav-link" href={this.props.item.url}>{this.props.item.name}</a>
            </li>
        )
    }
}

export default Navbar


