import React, { Component } from 'react'
import '../css/Preloader.css'
import $ from 'jquery'

class Preloader extends Component{
	componentDidMount(){
		$(document).ready(function(){
			$('#preloader').fadeOut(2000);
		})
	}
	render(){
		return(
			<div id="preloader"></div>
		)
	}
}


export default Preloader


