import React from 'react'

import Logo from '../images/icon.png'

class Home extends React.Component{
    render(){
    	var  ButtonStyle = {
    		marginTop : '-10px',
    		borderTopRightRadius: '0',
    		borderTopLeftRadius: '0'
    	}
    	var IntroBoxStyle = {
    		padding: '100px',
    		paddingTop: '50px',
    		paddingBottom:'50px',
    		background: '#343a40',
    		color: 'white'
    	}
        return(
            <div className="container-fluid home-container bg-light">
            	<div className="row main-row">
	            	<div className="col-sm-6 col-md-6 col-lg-6 col-xl-6">
	            		<RotatingIcon />
	            	</div>
	            	<div className="col-sm-6 col-md-6 col-lg-6 col-xl-6">
	            		<Quotes quote="IoT is transforming the everyday physical objects that surround us into an ecosystem of information that will enrich our lives."/>
	            		<div className="text-center">
	            			<button className="btn btn-lg btn-block btn-primary" style={ButtonStyle}>Know More</button>
            			</div>
	            	</div>
            	</div>
            	<div className="row">
            		<div className="text-center introboxstyle" style={IntroBoxStyle}>
            			<h3> We are the group of aspiring engineers who wish to bring some changes to the field of technology.</h3>
            		</div>
            	</div>
            	<div className="bg-light">
	            	<h1 className="text-center ot_head"><b>O</b>ur <b>T</b>eam</h1>
	            	<OurTeam />
            	</div>
            	<WhyUs />
            	<h1 className="text-center ot_head"><b>W</b>ant <b>T</b>o <b>W</b>ork <b>W</b>ith <b>U</b>s</h1>
            	<WantToWorkWithUs />
            	<Footer />
            </div>
        )
    }
}

class RotatingIcon extends React.Component{
    render(){
        var style = {
            'paddingTop': '90px'
        }
        return(
        	<div className="text-center">
            	<img src={Logo} style={style} alt='rotatingLogo' className='img-fluid rotatingicon'/>
        	</div>
        )
    }
}

class Quotes extends React.Component{
	render(){
		var style = {
			marginTop: '150px',
			lineHeight: '60px',
			background: 'rgba(0,0,0,0.6)',
			padding: '30px',
			color: 'white',
			borderTopRightRadius: '5px',
    		borderTopLeftRadius: '5px'
		}
		return(
			<h2 className="text-left quotes" style={style}>{this.props.quote}</h2>
		)
	}
}

